import * as msgActionTypes from './types'

export const msgActions = {
	loadUsers: (users) => {
		return dispatch => {
			dispatch({type: msgActionTypes.LOAD_USERS_SUCCESS, payload: users})
		}
	},
	loadDirectConversations: (directConversations) => {
		return dispatch => {
			dispatch({type: msgActionTypes.LOAD_DIRECT_CONVERSATIONS_SUCCESS, payload: directConversations})
		}
	},
	updateCurrentConversation: (type, conversationID) => {
		return dispatch => {
			dispatch({type: msgActionTypes.UPDATE_CURRENT_CONVERSATION, payload: {type, conversationID}})
		}
	},
	updateDirectCurrentConversationMessages: (conversationID, message) => {
		return dispatch => {
			dispatch({type: msgActionTypes.UPDATE_CURRENT_DIRECT_CONVERSATION_MESSAGES, payload: {type: 'dm', conversationID, message}})
		}
	},
}