import MessageHeader from './MessageHeader'
import MessageFooter from './MessageFooter'
import MessageBody from './MessageBody'

import { useState} from 'react'
import { useDidMountEffect } from '../../customHooks/customEffects'
import { useDispatch, useSelector } from 'react-redux'

import {msgActions} from '../services/actions'

function getMessages(currentConversation, conversations) {
	if(currentConversation.type === 'dm'){
		return conversations.directConversations.filter(conv => currentConversation.conversationID === conv._id)[0]
	}
	else {
		return conversations.groupConversations.filter(conv => currentConversation.conversationID === conv._id)[0]
	}
}

function Message() {
	const dispatch = useDispatch()
	const { currentConversation, conversations } = useSelector(state => state.messaging)
	const [currConv, setCurrConv] = useState(conversations.directConversations[0])
	
	useDidMountEffect(() => {
		const currCoonv = getMessages(currentConversation, conversations)
		setCurrConv(currCoonv)
	},[currentConversation])

	return (
		<div>
		{conversations.directConversations.length>0 &&
			<div className="message-body"> 
				<MessageHeader oppUser={currConv.oppUser}/>
				<MessageBody conversation={currConv}/>
				<MessageFooter />
			</div>
		}
		</div>
	)
}

export default Message