import * as msgActionTypes from './types'

const initialState = {
    users: [],
    groups: [],
    conversations: {
        directConversations: [],
        groupConversations: []
    },
    currentConversation: {
        type: '',
        conversationID: ''
    },
    homeSocket: {}
};

const messaging = (state = initialState, action) => {
    switch (action.type) {
        case (msgActionTypes.LOAD_USERS_SUCCESS):
            return {
                ...state,
                users: action.payload
            };
        case (msgActionTypes.LOAD_DIRECT_CONVERSATIONS_SUCCESS):
            return {
                ...state,
                conversations: {
                    ...state.conversations,
                    directConversations: action.payload
                }
            };
        case (msgActionTypes.UPDATE_CURRENT_CONVERSATION):
            return {
                ...state,
                currentConversation: action.payload
            };
        case (msgActionTypes.UPDATE_CURRENT_DIRECT_CONVERSATION_MESSAGES):
            const convList = state.conversations.directConversations.map(conv => {
                if(conv._id === action.payload.conversationID){
                    conv.messages.unshift(action.payload.message)
                }
                return conv
            })
            return {
                ...state,
                conversations: {
                    ...state.conversations,
                    directConversations: convList
                }
            }
        default:
            return state;
    };
};

export default messaging;
