import * as authConst from './types';

const initialState = {
    login: {
        state: '',
        loading: false,
        error: null,
    },
    signup: {
        state: '',
        loading: false,
        error: null,
        message: null,
    }
};

const auth = (state = initialState, action) => {
    const { payload } = action;
    switch (action.type) {
        case (authConst.GOOGLE_LOGIN_REQUEST):
            return {
                ...state,
                login: {
                    ...state.login,
                    state: '',
                    loading: true,
                    error: null,
                }
            };
        case (authConst.GOOGLE_LOGIN_SUCCESS):
            return {
                ...state,
                login: {
                    ...state.login,
                    state: 'success',
                    loading: false,
                }
            };
        case (authConst.GOOGLE_LOGIN_FAILURE):
            return {
                ...state,
                login: {
                    ...state.login,
                    state: 'failed',
                    loading: false,
                    error: payload,
                }
            };
        case (authConst.GOOGLE_SIGNUP_REQUEST):
            return {
                ...state,
                login: {
                    ...state.login,
                    state: '',
                    loading: true,
                    error: null,
                    message: null
                }
            };
        case (authConst.GOOGLE_SIGNUP_SUCCESS):
            return {
                ...state,
                login: {
                    ...state.login,
                    state: 'success',
                    loading: false,
                    message: payload,
                }
            };
        case (authConst.GOOGLE_SIGNUP_FAILURE):
            return {
                ...state,
                login: {
                    ...state.login,
                    state: 'failed',
                    loading: false,
                    error: payload,
                    message: null
                }
            };

        default:
            return state;
    };
};

export default auth;
