import { authServices } from './api';
import * as authConst from './types';

export const authActions = {
    googleLogin: function (authCode) {
        return dispatch => {
            dispatch({ type: authConst.GOOGLE_LOGIN_REQUEST });
            authServices.googleLogin(authCode)
            .then(res => {
                let payload = res.data.accessToken;
                localStorage.setItem("accessToken", payload);
                dispatch({ type: authConst.GOOGLE_LOGIN_SUCCESS });
                window.location.reload();
            })
            .catch(err => {
                let payload = err.response.data.error;
                dispatch({ type: authConst.GOOGLE_LOGIN_FAILURE, payload })
            });
        };
    },
    googleSignUp: function (authCode) {
        return dispatch => {
            dispatch({ type: authConst.GOOGLE_SIGNUP_REQUEST });
            authServices.googleSignUp(authCode)
            .then(res => {
                let payload = res.data.accessToken;
                localStorage.setItem("accessToken", payload);
                dispatch({ type: authConst.GOOGLE_SIGNUP_SUCCESS, payload });
                window.location.reload();
            })
            .catch(err => {
                let payload = "!Error";
                dispatch({ type: authConst.GOOGLE_SIGNUP_FAILURE, payload })
            })
        };
    },
};