import { useState, useEffect } from 'react'
import { useDispatch } from 'react-redux'
import { authActions } from '../services/actions'


export function GoogleLogin() {
	const [auth2, setAuth2] = useState({})
	const dispatch = useDispatch()

    useEffect(() => {
    	window.gapi.load('auth2', () => {
			const auth2 = window.gapi.auth2.init({
				client_id: "352936315579-n8j9c0qe63m677g9fjt178821n0v4a0f.apps.googleusercontent.com",
			})
			setAuth2(auth2)
		})
    },[])

	const handleGoogleLogin = (e) => {
		e.preventDefault()
	    auth2.grantOfflineAccess()
	    .then(authResult => {
	        if (!authResult['code']) console.log("Error occurred")
	        dispatch(authActions.googleLogin(authResult['code']))
	    })
	    .catch(err => {console.log(err)})
	}

	return(
		<div>
			<button className = "googleLogin" onClick = {handleGoogleLogin}>
			Login With Google
			</button>
		</div>
	)
}