import { homeSocket } from '../../../routes/PrivateRoutes'

function Contacts(props) {
	const handleClick = e => {
		e.preventDefault()
		let currNode = e.target
		while(currNode.className !== 'direct-user-container') currNode = currNode.parentNode
		alert('Say Hello')
		// homeSocket.emit('startConversation', {receiverID: currNode.id, text: "Hello"}, (error, res) => {
		// 	if(!error){
		// 		console.log(res)
		// 	}
		// 	else console.log(error)
		// })
	}

	return (
		<div>
			<div className="direct-user-container" id={props.user._id} onClick={handleClick}>
				<div className="user-profile-img">
					<img src={props.user.profilePic} alt="User Profile Picture" width="25" height="27" />
				</div>
				<div className="username">
					{props.user.firstName} {props.user.lastName}
				</div>
			</div>
		</div>
	)
}

export default Contacts