import { useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { msgActions } from '../services/actions'
import { homeSocket } from '../../../routes/PrivateRoutes'

function MessageFooter() {
	const dispatch = useDispatch()
	const [text, setText] = useState('')
	const { currentConversation } = useSelector(state => state.messaging)
	const handleChange = (e) => {
		setText(e.target.value)
		e.target.style.height = "auto"
		e.target.style.height = (e.target.scrollHeight) + 'px'
	}
	const handleSubmit = (e) => {
		e.preventDefault()
		if(text.length == 0) return
		homeSocket.emit('sendDM', {conversationID: currentConversation.conversationID, text}, (error, res) => {
			if(!error){
				console.log(res)
				dispatch(msgActions.updateDirectCurrentConversationMessages(res.message.conversationID, res.message ))
			}
			else console.log(error)
		})
	}
	return(
		<div>
			<form className="message-footer" onSubmit={handleSubmit}>
				<textarea className="message-input" onChange={handleChange} placeholder="Type your message..">
				</textarea>
				<button type="submit"> SEND </button>
			</form>
		</div>
	)
}

export default MessageFooter	