function MessageHeader(props) {
	return(
		<div>
			<div className="message-header">
				<div className="user-profile-img">
						<img src={props.oppUser.profilePic} alt="User Profile Picture" width="25" height="27" />
					</div>
					<div className="username">
						{props.oppUser.firstName} {props.oppUser.lastName}
					</div>
				</div>
		</div>
	)
}

export default MessageHeader