import { useState, useEffect } from 'react'
import { useDispatch, useSelector} from 'react-redux'

import DirectConversations from '../components/messaging/conversations/DirectConversations'
import GroupConversations from '../components/messaging/conversations/GroupConversations'
import Message from '../components/messaging/messages/Message'
import Contacts from '../components/messaging/conversations/Contacts'
import { msgActions } from '../components/messaging/services/actions'
import {homeSocket} from '../routes/PrivateRoutes'

function MessagingPage() {
	const dispatch = useDispatch()
	const { users, conversations } = useSelector(state => state.messaging)
	useEffect(() => {
		homeSocket.emit('loadUsers', (error, res) => {
			if(!error){
				dispatch(msgActions.loadUsers(res.users))
			}
			else console.log(error)
		})
		homeSocket.on('newDM', data => {
			dispatch(msgActions.updateDirectCurrentConversationMessages(data.message.conversationID, data.message))
		})
	},[])
	useEffect(() => {
		homeSocket.emit('loadDirectConversations', {skip: 0, limit:5}, (error, res) => {
			if(!error){
				dispatch(msgActions.loadDirectConversations(res.directConversations))
				dispatch(msgActions.updateCurrentConversation('dm', res.directConversations[0]._id))
			}
			else console.log(error)
		})
	},[])

	return (
		<div>
			<div className="messaging-page-body">
				<div className="conversation-body">
					<DirectConversations />
					<GroupConversations />
				</div>
				{conversations.directConversations.length>0 && 
					<div className="message-body">
						<Message />
					</div>
				}
				<div className="contacts-body">
					<div className="conversations-list">
					{
						users.map(user => {
							return (
								<Contacts key={user._id}
								user={user} />
							)
						})
					}
					</div>
				</div>
			</div>
		</div>
	)
}

export default MessagingPage