import apiMethods from "../../../utils/apiMethods"
import { baseURL } from '../../../constants/api.constants'

export const authServices = {
    googleLogin: function (authCode) {
        return apiMethods.post(`${baseURL}/api/user/login/google`, { authCode });
    },
    googleSignUp: function (authCode) {
        return apiMethods.post(`${baseURL}/api/user/signUp/google`, { authCode });
    },
};


