import MessageBox from './MessageBox'
import { useEffect, useRef } from 'react'
// import { useSelector } from 'react-redux'

function MessageBody(props) {
	let {oppUser, messages} = props.conversation
	const message_list = useRef(null)
	useEffect(() => {
		message_list.current.scrollTop = 2000
	},[])
	return (
		<div>
			<div className="scrollbar" id="style-6"  ref={message_list}>
		      <div className="force-overflow message-list">
		      	{
		      		messages.map(message => {
		      			return(
      						<MessageBox key={message._id}
      						messageData={{oppUser:oppUser, message:message}}
      						/>
	      				)
		      		})
		      	}
		      </div>
		    </div>
		</div>
	)
}

export default MessageBody