import { useDispatch, useSelector } from 'react-redux'
import { msgActions } from '../services/actions'

function DirectUser(props) {
	const dispatch = useDispatch()
	const { currentConversation, conversation } = useSelector(state => state.messaging)
	const handleClick = e => {
		e.preventDefault()
		let currNode = e.target
		while(currNode.className !== 'direct-user-container') currNode = currNode.parentNode
		dispatch(msgActions.updateCurrentConversation('dm', currNode.id))
	}

	return (
		<div>
			<div className="direct-user-container" id={props.conv._id} onClick={handleClick}>
				<div className="user-profile-img">
					<img src={props.conv.oppUser.profilePic} alt="User Profile Picture" width="25" height="27" />
				</div>
				<div className="username">
					{props.conv.oppUser.firstName} {props.conv.oppUser.lastName}
				</div>
			</div>
		</div>
	)
}

export default DirectUser