function MessageBox(props) {
	const {oppUser, message} = props.messageData
	return (
		<div>
			{(oppUser._id === message.senderID) &&
			<div className="message-wrapper-left">
				<div className="profile-img" style={{margin: "5px", padding: "2px"}}>
					<img src={oppUser.profilePic} alt="User Profile Picture" width="25" height="27" />
				</div>
				{message.text &&
					<div className="message-container">
						{message.text}
					</div>
				}
			</div>
			}
			{(oppUser._id !== message.senderID) &&
			<div className="message-wrapper-right">
				{message.text &&
					<p className="message-container" style={{backgroundColor:"lightblue"}}>
						{message.text}
					</p>
				}
			</div>
			}
		</div>
	)
}

export default MessageBox