// import { useState, useEffect } from 'react'
import { useDispatch, useSelector} from 'react-redux'
// import { homeSocket } from '../../../routes/PrivateRoutes'
// import { msgActions } from '../services/actions'
import DirectUser from './DirectUser'

function DirectConversations() {
	const { directConversations } = useSelector(state => state.messaging.conversations)
	return(
		<div>
			<div className="conversations-list">
			{
				directConversations.map(conv => {
					if(conv){
						return (
							<DirectUser key={conv._id} conv={conv} />
						)
					}
				})
			}
			</div>
		</div>
	)
}

export default DirectConversations